<script>
	export let name;
	export let place;
	export let dataset;
	export let value;
	export let total;
	export let unit;
	
	let set = unit === '%' ? 'perc' : 'val';
	
	let breaks = [
		Math.round(total / 5),
		Math.round((2 * total) / 5),
		Math.round((3 * total) / 5),
		Math.round((4 * total) / 5),
	];
	
	$: rank = place.data[dataset].rank.c2011[value];
	$: quin = rank < breaks[0] ? 'q5' : rank < breaks[1] ? 'q4' : rank < breaks[2] ? 'q3' : rank < breaks[3] ? 'q2' : 'q1';
	
	$: crank = place.data[dataset].rank.change[value];
	$: cquin = crank < breaks[0] ? 'q5' : crank < breaks[1] ? 'q4' : crank < breaks[2] ? 'q3' : crank < breaks[3] ? 'q2' : 'q1';
</script>

<style>
	.muted {
		color: #555;
	}
	.positive {
		color: #008c4e;
	}
	.negative {
		color: #f04b22;
	}
	.quin {
		color: white;
		padding: 0 3px;
	}
	.q1 {
		background-color: #f04b22;
	}
	.q2 {
		background-color: #f68620;
	}
	.q3 {
		background-color: #ffcb06;
	}
	.q4 {
		background-color: #7ec349;
	}
	.q5 {
		background-color: #008c4e;
	}
</style>

<li>{name}: <strong>{place.data[dataset][set].c2011[value].toLocaleString()}{unit}</strong> <small class="quin {quin}">{place.data[dataset].rank.c2011[value]}</small> 
{#if place.data[dataset][set].change[value] < 0}
  <span class="negative">&#9662;{Math.abs(place.data[dataset][set].change[value])}%</span>
{:else if place.data[dataset][set].change[value] > 0}
  <span class="positive">&#9652;{place.data[dataset][set].change[value]}%</span>
{:else}
  <span class="muted">{place.data[dataset][set].change[value]}%</span>
{/if}
<small class="quin {cquin}">{place.data[dataset].rank.change[value]}</small></li>
